*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  min-width: 320px;
}

@media only screen and (max-width: 1250px) {
  html {
    font-size: 47%;
  }
}

@media only screen and (max-width: 835px) {
  html {
    font-size: 42%;
  }
}

@media only screen and (max-width: 685px) {
  html {
    font-size: 37%;
  }
}

@media only screen and (max-width: 615px) {
  html {
    font-size: 33%;
  }
}

@media only screen and (max-width: 510px) {
  html {
    font-size: 30%;
  }
}

body {
  box-sizing: border-box;
}

/*This is here because react-anchor-link-smooth-scroll and styled-components don't play nice together*/

a[href^='#'] {
  margin: 0 2.6rem;
  letter-spacing: 0.2rem;
  font-size: 2rem;
  white-space: nowrap;
}

a[href^='#']:link,
a[href^='#']:visited {
  color: rgb(242, 242, 242);
  text-decoration: none;
}

@media only screen and (max-width: 1250px) {
  a[href^='#'] {
    font-size: 1.9rem;
    margin: 0 2rem;
  }
}

@media only screen and (max-width: 835px) {
  a[href^='#'] {
    display: none;
  }
}

a.header-anchor {
  display: inline-block;
  position: relative;
  top: 0;
  left: 0;
  letter-spacing: 0.3rem;
  color: rgb(240, 240, 240);
  font-family: 'Play', sans-serif;
  font-size: 2.5rem;
  border: 0.3rem solid rgb(245, 245, 245);
  border-width: 0 0 0.2rem 0;
  padding: 0.5rem 1rem 0.7rem 1rem !important;
  background-color: transparent;
  font-weight: 10;
  cursor: pointer;
  margin: 0 !important;
  letter-spacing: 0.2rem;
  transition: all 0.4s;
}

@media only screen and (max-width: 1250px) {
  a.header-anchor {
    font-size: 3.5rem;
  }
}

@media only screen and (max-width: 835px) {
  a.header-anchor {
    font-size: 4rem;
  }
}

a.header-anchor:hover {
  transform: scale(1.1);
}
